.nav-links {
    list-style: none;
    margin: 0;
    padding: 0;
    width: 100%;
    height: 100%;
    display: inline-flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
  }
  
  .nav-links ul {
  }

  .nav-links li {
      height:100%;
  }
  
.link-container {
    margin-right: 24px;
    height:100%;
    display:flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    border-bottom-color: blue;
    border-width: 0px;
    text-align: center;
    padding:10px;
}


.nav-links a {
  color: #fff;
  text-decoration: none;
  font-size:20px;
  font-weight: bold;
  padding: 3px 5px;
}

.nav-links a:after {
  display:block;
  content: '';
  border-bottom: solid 3px #fff;  
  -webkit-transform: scaleX(0);  
          transform: scaleX(0);  
  -webkit-transition: -webkit-transform 250ms ease-in-out;  
  transition: -webkit-transform 250ms ease-in-out;  
  transition: transform 250ms ease-in-out;  
  transition: transform 250ms ease-in-out, -webkit-transform 250ms ease-in-out;
}
.nav-links a:hover{
    color:#E2C6AD;
}
.nav-links a:hover:after { -webkit-transform: scaleX(1); transform: scaleX(1); }
.nav-links a:after{  -webkit-transform-origin:  0% 50%;  transform-origin:  0% 50%; }

.nav-links a:active,
.nav-links a.active {
  
  background-color: rgba(159, 191, 215,0.3);
  color:#fff;
  font-weight: bolder;
}

@media screen and (max-width:960px) {
  .nav-links a {
    font-size:18px;
    padding: 5px;
  }
  .link-container{
    margin-right : 10px;
    padding:5px;
  }
}

@media screen and (max-width:550px) {
  .nav-links a {
    font-size:15px;
    padding: 0px;
  }
}

