.side-drawer {
  position: fixed;
  right: 0;
  top: 50px;
  z-index: 100;
  height: 100vh;
  width: 70%;
  background: black;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.26);
}

.backdrop {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  background: rgba(0, 0, 0, 0.75);
  z-index: 10;
}


