.content-wrapper {
    position:relative;
    overflow: hidden;
    max-width: 100%;
    max-height: 100%;
    display:flex;
    flex-direction: row;
}

.content-holder {
    overflow:hidden;
    position:relative;
    max-width:100%;
    flex:1 1;
}

.image-small {
    position:relative;
    width :100%;
    height:100%;
    /* border: 1px solid #B57C65; */
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center center;
    -webkit-transition: 0.3s;
    transition: 0.3s;
    opacity: 0.7;
    z-index: 1;
}

.image-indicator {
    position:absolute;
    width:10px;
    height:40px;
    top:0px;
    left:0px;
    background-color: #CF9D7B;
    z-index: 5;
    border-bottom: 2px solid #E2C6AD;
    border-right: 1px solid  #E2C6AD;
}

.image-detail {
    position:absolute;
    bottom:20px;
    right:20px;
    background-color: transparent;
    border : 1px solid white;

    color:white;
    text-align:center;
    box-shadow: 2px 2px 2px grey;
    z-index: 2;
}

.image-detail a {
    margin:0px;
    padding:8px;
    cursor:pointer;
    font-family: 'Nanum Gothic', sans-serif;
    font-size: 14px;
    /* background-color: rgba(255,255,255,0.3); */
}

.image-small:hover {
    cursor:pointer;
    opacity:1.0;
}

.image-detail:hover {
    background-color: #135E46;
    
}

.image-small:hover + .image-detail {
    background-color: #135E46;
}

